function hexToRGB(code) {
    let rStr = code.substring(0, 2)
    let gStr = code.substring(2, 4)
    let bStr = code.substring(4, 6)

    

    return {
        r: parseInt(rStr, 16),
        g: parseInt(gStr, 16),
        b: parseInt(bStr, 16)
    }
}

